<template>
<Card>
  <p slot="title">
    {{title}}
  </p>
  <editorProject></editorProject>
</Card>
</template>
<script>
import editorProject from '../components/editors/editor-project.vue'
import bread from '../mixin/bread'
export default {
  name: 'projectEditor',
  data () {
    return {
    }
  },
  mixins: [bread],
  breadList () {
    let option = [
      { name: '', menuName: '' },
      { name: '' },
    ]
    if (this.$route.query.id) option[1].name = ''
    if (this.$route.query.fromDetail) option.splice(1, 0, {name: ''})
    return option
  },
  computed: {
    title () {
      return this.$route.query.id ? '' : ''
    },
  },
  components: {
    editorProject,
  },
  watch: {

  },
  mounted () {

  },
  methods: {
  },
}
</script>



